/* ========================================
   Global Styles - Talaqi Platform
   ======================================== */

/* CSS Custom Properties (Variables) */
:root {
    /* Brand Colors - Professional Blue Theme */
    --primary: #324260;
    --primary-dark: #243141;
    --primary-light: #3d5270;
    --accent: #6C8DB5;
    --accent-dark: #5a7a9d;
    --accent-light: #8aa5c7;

    /* Semantic Colors */
    --success: #10b981;
    --success-light: #34d399;
    --danger: #ef4444;
    --danger-light: #f87171;
    --warning: #f59e0b;
    --warning-light: #fbbf24;
    --info: #6C8DB5;
    --info-light: #8aa5c7;

    /* Text Colors */
    --text-primary: #1A1A1A;
    --text-secondary: #787878;
    --text-light: #FFFFFF;

    /* Neutral Colors */
    --white: #FFFFFF;
    --background: #FFFFFF;
    --border: #DDDDDD;
    --gray-50: #f9fafb;
    --gray-100: #f3f4f6;
    --gray-200: #e5e7eb;
    --gray-300: #d1d5db;
    --gray-400: #9ca3af;
    --gray-500: #787878;
    --gray-600: #4b5563;
    --gray-700: #374151;
    --gray-800: #1f2937;
    --gray-900: #1A1A1A;

    /* Gradients */
    --gradient-primary: linear-gradient(135deg, #324260 0%, #3d5270 100%);
    --gradient-accent: linear-gradient(135deg, #6C8DB5 0%, #8aa5c7 100%);
    --gradient-overlay: linear-gradient(135deg, rgba(50, 66, 96, 0.9) 0%, rgba(108, 141, 181, 0.9) 100%);

    /* Shadows */
    --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    --shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
    --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
    --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);

    /* Spacing */
    --spacing-xs: 0.25rem;
    --spacing-sm: 0.5rem;
    --spacing-md: 1rem;
    --spacing-lg: 1.5rem;
    --spacing-xl: 2rem;
    --spacing-2xl: 3rem;

    /* Border Radius */
    --radius-sm: 0.375rem;
    --radius-md: 0.5rem;
    --radius-lg: 0.75rem;
    --radius-xl: 1rem;
    --radius-2xl: 1.5rem;
    --radius-full: 9999px;

    /* Transitions */
    --transition-fast: 150ms cubic-bezier(0.4, 0, 0.2, 1);
    --transition-base: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    --transition-slow: 350ms cubic-bezier(0.4, 0, 0.2, 1);

    /* Fonts */
    --font-arabic: 'Tajawal', -apple-system, BlinkMacSystemFont, sans-serif;
    --font-latin: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
}

/* ========================================
   Base Styles
   ======================================== */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: var(--font-arabic);
    direction: rtl;
    text-align: right;
    background-color: var(--background);
    color: var(--text-primary);
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
}

/* ========================================
   Custom Scrollbar
   ======================================== */
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

::-webkit-scrollbar-track {
    background: var(--gray-100);
}

::-webkit-scrollbar-thumb {
    background: var(--gradient-accent);
    border-radius: var(--radius-full);
    border: 2px solid var(--gray-100);
}

::-webkit-scrollbar-thumb:hover {
    background: var(--gradient-primary);
}

/* ========================================
   Bootstrap Overrides
   ======================================== */
.btn-primary {
    background: var(--accent);
    border: none;
    color: var(--text-light);
    box-shadow: var(--shadow-sm);
    transition: all var(--transition-base);
    font-weight: 500;
}

.btn-primary:hover {
    background: var(--accent-dark);
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
    color: var(--text-light);
}

.btn-primary:active,
.btn-primary:focus {
    background: var(--accent-dark);
    transform: translateY(0);
    color: var(--text-light);
    box-shadow: var(--shadow-sm);
}

.btn-outline-primary {
    border-color: var(--accent);
    color: var(--accent);
    transition: all var(--transition-base);
}

.btn-outline-primary:hover {
    background: var(--accent);
    border-color: var(--accent);
    color: var(--text-light);
    transform: translateY(-2px);
    box-shadow: var(--shadow-sm);
}

.btn-secondary {
    background: var(--primary);
    border: none;
    color: var(--text-light);
    transition: all var(--transition-base);
}

.btn-secondary:hover {
    background: var(--primary-dark);
    color: var(--text-light);
}

.card {
    border: 1px solid var(--border);
    box-shadow: var(--shadow-sm);
    transition: all var(--transition-base);
    border-radius: var(--radius-lg);
    background: var(--white);
}

.card:hover {
    box-shadow: var(--shadow-md);
}

.card-header {
    background-color: var(--gray-50);
    border-bottom: 1px solid var(--border);
    color: var(--text-primary);
    font-weight: 600;
}

.card-footer {
    background-color: var(--gray-50);
    border-top: 1px solid var(--border);
}

.form-control,
.form-select {
    border-color: var(--border);
    color: var(--text-primary);
    transition: all var(--transition-base);
}

.form-control:focus,
.form-select:focus {
    border-color: var(--accent);
    box-shadow: 0 0 0 0.2rem rgba(108, 141, 181, 0.25);
    color: var(--text-primary);
}

.form-label {
    color: var(--text-primary);
    font-weight: 500;
    margin-bottom: 0.5rem;
}

.badge {
    padding: 0.35rem 0.75rem;
    border-radius: var(--radius-full);
    font-weight: 500;
}

.badge-primary {
    background-color: var(--accent);
    color: var(--text-light);
}

.badge-secondary {
    background-color: var(--gray-500);
    color: var(--text-light);
}

.badge-success {
    background-color: var(--success);
    color: var(--text-light);
}

.badge-danger {
    background-color: var(--danger);
    color: var(--text-light);
}

.badge-warning {
    background-color: var(--warning);
    color: var(--text-light);
}

.badge-info {
    background-color: var(--info);
    color: var(--text-light);
}

.text-primary {
    color: var(--text-primary) !important;
}

.text-secondary {
    color: var(--text-secondary) !important;
}

.text-muted {
    color: var(--text-secondary) !important;
}

.bg-primary {
    background-color: var(--primary) !important;
}

.bg-light {
    background-color: var(--gray-50) !important;
}

.border {
    border-color: var(--border) !important;
}

.border-top,
.border-bottom,
.border-start,
.border-end {
    border-color: var(--border) !important;
}

/* ========================================
   Utility Classes
   ======================================== */

/* Gradients */
.gradient-primary {
    background: var(--gradient-primary);
}

.gradient-accent {
    background: var(--gradient-accent);
}

.gradient-text {
    background: var(--gradient-accent);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

/* Glassmorphism */
.glass {
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid var(--border);
}

.glass-dark {
    background: rgba(50, 66, 96, 0.9);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Shadows */
.shadow-glow {
    box-shadow: 0 0 20px rgba(108, 141, 181, 0.3);
}

.shadow-glow-hover:hover {
    box-shadow: 0 0 30px rgba(108, 141, 181, 0.5);
}

/* ========================================
   Animations
   ======================================== */

/* Fade In */
@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.fade-in {
    animation: fadeIn var(--transition-base) ease-in;
}

/* Slide Up */
@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.slide-up {
    animation: slideUp var(--transition-slow) ease-out;
}

/* Slide Down */
@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.slide-down {
    animation: slideDown var(--transition-slow) ease-out;
}

/* Scale In */
@keyframes scaleIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

.scale-in {
    animation: scaleIn var(--transition-base) ease-out;
}

/* Pulse */
@keyframes pulse {

    0%,
    100% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.05);
    }
}

.pulse {
    animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Shimmer Loading */
@keyframes shimmer {
    0% {
        background-position: -1000px 0;
    }

    100% {
        background-position: 1000px 0;
    }
}

.shimmer {
    background: linear-gradient(90deg, var(--gray-200) 0%, var(--gray-300) 20%, var(--gray-200) 40%, var(--gray-200) 100%);
    background-size: 1000px 100%;
    animation: shimmer 2s linear infinite;
}

/* Float */
@keyframes float {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-10px);
    }
}

.float {
    animation: float 3s ease-in-out infinite;
}

/* Gradient Animation */
@keyframes gradientShift {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

.gradient-animated {
    background: linear-gradient(270deg, #324260, #6C8DB5, #8aa5c7, #324260);
    background-size: 400% 400%;
    animation: gradientShift 15s ease infinite;
}

/* Bounce */
@keyframes bounce {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-5px);
    }
}

.bounce {
    animation: bounce 1s ease infinite;
}

/* Spin */
@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.spin {
    animation: spin 1s linear infinite;
}

/* ========================================
   Hover Effects
   ======================================== */
.hover-lift {
    transition: all var(--transition-base);
}

.hover-lift:hover {
    transform: translateY(-5px);
    box-shadow: var(--shadow-lg);
}

.hover-scale {
    transition: transform var(--transition-base);
}

.hover-scale:hover {
    transform: scale(1.05);
}

.hover-brightness {
    transition: filter var(--transition-base);
}

.hover-brightness:hover {
    filter: brightness(1.1);
}

/* ========================================
   Loading States
   ======================================== */
.skeleton {
    background: var(--gray-200);
    border-radius: var(--radius-md);
    position: relative;
    overflow: hidden;
}

.skeleton::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.5), transparent);
    animation: shimmer 2s infinite;
}

/* ========================================
   Responsive Utilities
   ======================================== */
@media (max-width: 768px) {
    body {
        font-size: 14px;
    }

    .card {
        margin-bottom: var(--spacing-md);
    }
}

/* ========================================
   Print Styles
   ======================================== */
@media print {
    .no-print {
        display: none !important;
    }
}